:root {
  --black: #111;
  --theme: #678;
  --background: #F1F3F5;
  --blue: #00BFED;
  --light-green: #00D7C5;
  --purple: #AD1DE6;
  --bg-black: #303E4B;
  --yellow: #E99523;
  --red: #D81C60;
  --font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: -moz-none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  color: #111;
  color: var(--black);
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #678;
  background-color: var(--theme);
  outline: 1px solid #111;
  outline: 1px solid var(--black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


header {
    width: 100%;
    height: 12vh;
    position: fixed;
    z-index: 10;
    top: 0;
}

#header-container {
    width: 87%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#header-right ul {
    list-style: none;
}

#header-right li {
    display: inline-block;
    margin-left: 3.5vw;
}

#header-right a {
    text-decoration: none;
    color: var(--black);
}

main {
    width: 100%;
    margin-top: 12vh;
}

footer {
    width: 100%;
    height: 15vh;
    bottom: 0;
    background: #eee;
    padding: 25px 0;
}

#footer-container {
    width: 75%;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: space-between;
}

#footer-container > * {
    flex: 1 1;
}

.footer-link {
    text-align: right;
}

.footer-link > *{
    display: inline-block;
}

.footer-link > *:last-child {
    margin-left: 2vw;
    width: 37px;
    text-align: center;
}

#claim {
    text-align: left;
    opacity: 0.7;
}

#claim small {
    font-size: 11px;
}
.link-button, .link-button:visited {
    cursor: pointer;
    position: relative;
    padding: 8px 30px;
    border-radius: 5px;
    box-shadow: 8px 10px 20px 0 rgba(46,61,73,.15);
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    text-decoration: none;
    text-align: center;
}

.link-button:hover, .link-button:focus {
    box-shadow: 2px 4px 8px 0 rgba(46,61,73,.25);
}
#home-page-container {
    padding: 7vh 0;
    text-align: center;
    color: var(--red);
    border: 1px solid var(--red);
}
